.slideShowWrapp {
	height: 100vh;
	width: 100%;
	overflow: hidden;
	position: absolute;
	left: 0;
	top: 0;
	z-index: 1300
}

.slideShowWrapp ul {
	list-style: none
}

.topOverlay li,
.bottomOverlay li {
	height: 12.52%;
	background: #2196f3;
	opacity: 0;
	position: absolute;
	width: 0
}

.topOverlay li {
	left: 0
}

.bottomOverlay li {
	right: 0
}

.topOverlay li:nth-child(1) {
	transition: all ease .2s;
	-moz-transition: all ease .2s;
	-ms-transition: all ease .2s;
	-webkit-transition: all ease .2s;
	-o-transition: all ease .2s;
	top: 0
}

.topOverlay li:nth-child(2) {
	transition: all ease .4s;
	-moz-transition: all ease .4s;
	-ms-transition: all ease .4s;
	-webkit-transition: all ease .4s;
	-o-transition: all ease .4s;
	top: 12.5%
}

.topOverlay li:nth-child(3) {
	transition: all ease .6s;
	-moz-transition: all ease .6s;
	-ms-transition: all ease .6s;
	-webkit-transition: all ease .6s;
	-o-transition: all ease .6s;
	top: 25%
}

.topOverlay li:nth-child(4) {
	transition: all ease .8s;
	-moz-transition: all ease .8s;
	-ms-transition: all ease .8s;
	-webkit-transition: all ease .8s;
	-o-transition: all ease .8s;
	top: 37.5%
}

.topOverlay.goToAnimate li:nth-child(1) {
	width: 100%!important;
	opacity: 1!important
}

.topOverlay.goToAnimate li:nth-child(2) {
	width: 100%!important;
	opacity: 1!important
}

.topOverlay.goToAnimate li:nth-child(3) {
	width: 100%!important;
	opacity: 1!important
}

.topOverlay.goToAnimate li:nth-child(4) {
	width: 100%!important;
	opacity: 1!important
}

.topOverlay.goToAnimate.goReverse li {
	left: auto;
	right: 0
}

.topOverlay.goToAnimate.goReverse li {
	width: 0!important;
	opacity: 0!important
}

.bottomOverlay li:nth-child(1) {
	transition: all ease .2s;
	-moz-transition: all ease .2s;
	-ms-transition: all ease .2s;
	-webkit-transition: all ease .2s;
	-o-transition: all ease .2s;
	bottom: 0
}

.bottomOverlay li:nth-child(2) {
	transition: all ease .4s;
	-moz-transition: all ease .4s;
	-ms-transition: all ease .4s;
	-webkit-transition: all ease .4s;
	-o-transition: all ease .4s;
	bottom: 12.5%
}

.bottomOverlay li:nth-child(3) {
	transition: all ease .6s;
	-moz-transition: all ease .6s;
	-ms-transition: all ease .6s;
	-webkit-transition: all ease .6s;
	-o-transition: all ease .6s;
	bottom: 25%
}

.bottomOverlay li:nth-child(4) {
	transition: all ease .8s;
	-moz-transition: all ease .8s;
	-ms-transition: all ease .8s;
	-webkit-transition: all ease .8s;
	-o-transition: all ease .8s;
	bottom: 37.5%
}

.bottomOverlay.goToAnimate li:nth-child(1) {
	width: 100%!important;
	opacity: 1!important
}

.bottomOverlay.goToAnimate li:nth-child(2) {
	width: 100%!important;
	opacity: 1!important
}

.bottomOverlay.goToAnimate li:nth-child(3) {
	width: 100%!important;
	opacity: 1!important
}

.bottomOverlay.goToAnimate li:nth-child(4) {
	width: 100%!important;
	opacity: 1!important
}

.bottomOverlay.goToAnimate.goReverse li {
	right: auto;
	left: 0;
	opacity: 0!important
}

.bottomOverlay.goToAnimate.goReverse li {
	width: 0!important
}

