.themeSlidShows{width:84%; overflow:hidden; margin:0 auto; margin-top:20px}
.themeSlidShows .postItem {display:none;}
@-webkit-keyframes fadediv {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes fadediv {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
.themeSlidShows .postItem.active {
    display: block;
    -webkit-animation: fadediv 1s forwards;
    animation: fadediv 1s forwards;
    -moz-animation: fadediv 1s forwards;
}
.themeSlidShows .flatCard{padding:0px}
.themeSlidShows .flatCard, .themeSlidShows .flatCard .post{ height:inherit; margin:0px; border:0px !important;}
.themeSlidShows .flatCard .tb_slide_w_half,.themeSlidShows .flatCard .tb_slide_w_full{ margin:0px; padding:0px; height:inherit; overflow:hidden;}
.themeSlidShows .tb_slide_w_half{ width:50% !important; clear:none !important; float:left !important; position: relative;}
.blackBgVideo{height:100%; background:#000;
  /* background-image: none !important; */
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.blackBgVideo:before{
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #00000094;
  filter: blur(10px);
  -webkit-filter: blur(10px);
  top: 0px;
  left: 0px;
}
.themeSlidShows .tb_fc_media_wrap{
  height: 100%;
}
.themeSlidShows .makeBgImg {
    background-size: contain !important;
    background-repeat: no-repeat !important;
    background-position: center center !important;
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 5;
    object-fit: contain;

}
