} */
.themeSlidShows .fb_iframe_widget span{
  width: 100% !important;
}

.themeSlidShows .flatCard .post img.img-responsive, .themeSlidShows .flatCard .post iframe,
.themeSlidShows .flatCard .post video{
  position: absolute;   
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  /* height: auto;  */
  width: 100% !important;
  height: 100% !important;
  max-height:100% !important; 
}
.themeSlidShows .flatCard .post .tb_slide_w_full video{
  width: auto;
  height: auto;
}
.themeSlidShows .tb_slide_w_full .tb_fc_media_wrap img{
  object-fit: contain;
}
.themeSlidShows .flatCard .post img.img-responsive{max-width:100%; visibility:hidden !important;}
.themeSlidShows .flatCard .postContentCard{ height:100%; padding:0px; position: relative;}
.themeSlidShows .flatCard .postContentCard *{text-align:center;}
.themeSlidShows .flatCard .postContentCard .postNetwork img{max-width: 100%}
.themeSlidShows .postContentClasic{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    width:96%;
}
.themeSlidShows .postedTxt{ font-size:30px; line-height:125%;} 

.themeSlidShows .flatCard.onlyTextPost .postedTxt.decorateTxt p::before, .themeSlidShows .flatCard.onlyTextPost .postedTxt.decorateTxt p::after, .themeSlidShows .flatCard.onlyTextPost .postedTxt.decorateTxt h1::before{font-family: taggboxsocialfonts;display: inline;font-size: inherit;} 
.themeSlidShows .flatCard.onlyTextPost .postedTxt.decorateTxt p::before, .themeSlidShows .flatCard.onlyTextPost .postedTxt.decorateTxt h1::before{content: "\f10d";margin: 0 10px 0 0;text-align:left;position: relative;top: -5px; font-weight:normal;}
.themeSlidShows .flatCard.onlyTextPost .postedTxt.decorateTxt p::after{content: "\f10e";margin: 5px 0 0 10px;text-align:right;position: relative;top: 5px;} 
.themeSlidShows .flatCard.onlyTextPost .postedTxt.decorateTxt h1{line-height: 1.3;}
.themeSlidShows .flatCard.onlyTextPost .postedTxt.decorateTxt h1 + p::before{ display:none;}


.themeSlidShows .authorDetails {position:static;}
.themeSlidShows .flatCard h4 img{ float:none; clear:both; margin:0px auto 7px auto; display:block;}
.themeSlidShows .authorDetails .authrHandle, .themeSlidShows .sepratedot,
.themeSlidShows .authorDetails .timePost{ float:none;}
.themeSlidShows .sepratedot{ margin: 2px 5px 0 5px; vertical-align: top; line-height: 18px;}
.themeSlidShows .flatCard h4 .postNetwork{float:none; clear:both; margin:7px 0 0 0;} 
.themeSlidShows .authorDetails .postAuthorName{ font-size:20px; line-height: 130%;}
.themeSlidShows .authorDetails img{ 
    height: 65px; width: 65px; 
    border: 2px solid #ffffff; 
    box-shadow: 0px 0px 1px rgba(0,0,0,0.7);
    -moz-box-shadow: 0px 0px 1px rgba(0,0,0,0.7);
    -webkit-box-shadow: 0px 0px 1px rgba(0,0,0,0.7);
}
.themeSlidShows .authorDetails .authrHandle{ font-size:16px;max-width:47%; line-height:130%;}
.themeSlidShows .authorDetails .authrHandleTime{ margin-top:5px;}
.themeSlidShows .authorDetails .timePost{ font-size: 14px;}

.themeSlidShows .postedTxt{color:#fff;}
.themeSlidShows .postedTxt h1{ margin-top:70px;}

.themeSlidShows .authorName span, .themeSlidShows .authorName a{ margin-top:2px;}
.themeSlidShows .postedTxt h1 + p{ margin-top:15px !important;} 
.themeSlidShows .postNetwork{      
    border-width:0px;
	width: 80px;
    height: 80px;
    line-height: 80px;
    border: 0px !important;
    background: none !important;
	margin:0 auto;
} 
.makeSocilCntr .postContentClasic{top:58%;}
.makeSocilCntr .postNetwork{ margin-bottom: 150px !important;}
.makeSocilCntr .authorDetails{ margin-bottom: -150px !important;	}

